@media (max-width: 650px) {
	.main .social-sidebar {
		display: block;
	}
}
.social-sidebar {
	position: relative;
	margin: auto;
	height: 100px;
	width: 100%;
	bottom: 0;
	right: 0;
}
.social-sidebar ul {
	flex-wrap: wrap;
}
.social-sidebar li a {
	text-align: center;
	line-height: 30px;
	padding: 0 12px;
	font-size: 15px;
	color: yellow;
	width: 100%;
}
@media (min-width: 350px) {
	.social-sidebar li a {
		font-size: 20px;
	}
}
@media (min-width: 500px) {
	.social-sidebar li a {
		font-size: 25px;
	}
}